.mask
{
    width: 100%;
    height: 100vh;
    min-height: 100%;
    position:  relative;
    overflow: hidden;

 
}

.intro-img
{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top : 0;
    bottom: 10%;
    left: 0;
    background: rgba(0,0,0,0.8);

}

.hero {
    height: 100%;
    width: 100%;
}
.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 40%;
    left: 50%;
    text-align: center;
}

.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
    
}

.hero .content p {
    font-size: 1.5rem;
    fonmt-weight: 200;
    text-transform: uppercase;
}

.content .btn
{
    margin: 1rem 0.6rem;
}

@media screen and (max-width: 640px) {
    .hero .content {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 57%;
        left: 50%;
        text-align: center;
    }
    .hero .content h1 {
        font-size: 3rem;
        
        
    }
    
    .hero .content p {
        font-size: 1.5rem;
        
    }
    

    
}
.avatar-container {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease;
  }
  
  .avatar:hover {
    transform: scale(1.5);
  }