.header
{
    display: flex;
    justify-content: space-between;
    allign-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;;

}
.header-bg {
    background-color: rgba(0,0,0, 0.75);

    transition: 0.5s;
}

.nav-menu{
    display: flex
;
}


.nav-menu li{
    padding: 0 1rem;  /* for spacing  */ 

}
.nav-menu li a{
    font-size:  1.2rem;
    font-widht: 500;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px) {
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        allign-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0,0,0,0.9);
        
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;

    }

    .nav-menu.active{
        left: 0%;
        
    }

    .nav-menu li{
        padding: 1rem 0;

    }
    .nav-menu li a {
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
    
}


.social-icons { 
    display: flex;
    align-items: center;
    margin-right: 1rem;
}

.social-icons a {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
}

.social-icons a:hover {
    transform: translateY(-3px);
    opacity: 0.8;
}

@media screen and (max-width: 1040px) {
    .social-icons {
        position: absolute;
        right: 90px;
    }
}