.project-heading
{
    text-align: center;
    padding: 4rem 0 2rem 0;
}
.project-container
{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap : 40px;
    padding-bottom: 5rem;

}

.project-card 
{
    background: #333;
    padding: 1.2rem 1rem;
}

.project-card img{
    width: 100%;
    transition: transform 0.3s ease;
}
.project-card img:hover {
    transform: scale(3);
}
.project-title
{
    color: #fff;
    padding: 1rem 0;
}
.project-details p{
    padding: bottom 1rem;
   
   
    text-align: justify;

}
.pro-btns
{
   display: flex;
   justify-content: space-between;
   padding: 0.5rem 0;
}
.pro-btns .btn{
    padding: 0.5rem 1rem;
    margin-right: 1rem;

}
.pro-btns .btn:last-child {
    margin-right: 0; /* Remove the margin from the last button */
}

@media screen and (max-width: 740px) {
    .project-container
    {
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;

    }

    .project-card img:hover {
        transform: scale(1.3);
    }

    
    
}